import React from 'react';
import SensorScreen from './SensorScreen';
import CssBaseline from '@material-ui/core/CssBaseline';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';

import { makeStyles } from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import List from '@material-ui/core/List';
import { GroupBy, Map } from 'react-lodash';
import NotificationEventsGroup from './NotificationEventsGroup';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { logs, addLog } from './utils/applogs';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  dialog2: {
    minWidth: "400px"
  }
}));

const reloadData = (setNotifictionEvents) => {
  addLog('reloadData');
  fetch(`https://aoq53tdls8.execute-api.us-east-1.amazonaws.com/dev/sensorNotificationEvent`)
  .then(response => response.json())
  .then(json => {
    setNotifictionEvents(json);
  })
  .catch((error) => {
    console.error('ERROR',error);
  });
}

export default function App() {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
    addLog('handleMenu');
  };

  const handleClose = () => {
    setAnchorEl(null);
    addLog('handleClose');
  };

  const [openEvents, setOpenEvents] = React.useState(false);
  const [openLogs, setOpenLogs] = React.useState(false);
  const [notificationEvents, setNotifictionEvents] = React.useState([]);

  const handleClickEvents = () => {
    setOpenEvents(false);
    handleClose();
  };

  const handleOpenEvents = () => {
    setOpenEvents(true);
  };

  const handleClickLogs = () => {
    setOpenLogs(false);
    handleClose();
  };

  const handleOpenLogs = () => {
    setOpenLogs(true);
  };

  React.useEffect(() => {
    reloadData(setNotifictionEvents);
  }, []);

  return (
    <React.Fragment>
      <CssBaseline />
      <div className={classes.root}>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton 
            edge="start" 
            className={classes.menuButton} 
            color="inherit" 
            aria-label="menu" 
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
          >
            <MenuIcon />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            open={open}
            onClose={handleClose}
          >
            <div>
              <MenuItem onClick={handleOpenEvents}>
                Notification Events
              </MenuItem>
              <Dialog onClose={handleClickEvents} aria-labelledby="simple-dialog-title" open={openEvents} fullWidth={true} maxWidth={'xs'}>
                <DialogTitle id="simple-dialog-title">Notification Events</DialogTitle>
                <List>
                  <GroupBy collection={notificationEvents} iteratee={(entry) => entry.sensorId}>
                    { (value) => 
                      <Map collection={Object.keys(value)} iteratee={key =>
                        <NotificationEventsGroup 
                          eventsList={value[key]} 
                          handleClickEvents={handleClickEvents} 
                          key={key}
                          snoozeFunction={(sensorId, hours) => {
                            addLog('snoozeFunction: ', sensorId);
                            fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensor/${sensorId}`, 
                              {
                                headers: {
                                  'Accept': 'application/json',
                                  'Content-Type': 'application/json'
                                },
                                method: 'PATCH',
                                body: JSON.stringify( { snoozeUntil: new Date().getTime() + (1000 * 24 * 60 * 60) } )
                              }
                            )
                              .then(response => response.json())
                              .then(json => {
                                addLog('Success', json);
                              })
                              .catch((error) => {
                                console.error('ERROR', error);
                              });
                          }}
                        />
                      } />
                    }
                  </GroupBy>
                </List>
              </Dialog>
            </div>
            <div>
              <MenuItem onClick={handleOpenLogs}>
                Application Logs
              </MenuItem>
              <Dialog onClose={handleClickLogs} aria-labelledby="simple-dialog-title" open={openLogs} fullWidth={true} maxWidth={'xs'}>
                <DialogTitle id="simple-dialog-title">Application Logs</DialogTitle>
                <List>
                      <Map collection={logs()} iteratee={logrow =>
                        <ListItem>
                          <ListItemText primary={logrow} />
                        </ListItem>
                      } />
                </List>
              </Dialog>
            </div>
          </Menu>
        </Toolbar>
      </AppBar>
      <Toolbar/>
      </div>
      <SensorScreen />
    </React.Fragment>
  );
}