import React from 'react';
import SensorList from './SensorList';
import Container from '@material-ui/core/Container';
import { addLog } from './utils/applogs';

const groupBy = (array, prop) => {
  return array.reduce(function(groups, item) {
    const val = item[prop]
    groups[val] = groups[val] || []
    groups[val].push(item)
    return groups
  }, {})
}

const reloadData = (setSensorData, fromDateTime = new Date(new Date().getTime() - (6*3600*1000)), toDateTime = new Date()) => {
  addLog('reloadData', fromDateTime, toDateTime);
//    fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensorValues?fromDateTime=${parseInt(fromDateTime.getTime()/1000,10)}&toDateTime=${parseInt(toDateTime.getTime()/1000,10)}`)
  fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensorValues?from=6`)
  .then(response => response.json())
  .then(json => {
    setSensorData({
      sensorData: { 
        items: groupBy(json.series, 'sensorid'), 
        lastUpdated: new Date()
      }
    });
  })
  .catch((error) => {
    console.error('ERROR',error);
  });
}

export default function SensorsScreen() {
  const [state, setSensorData] = React.useState({
    sensorData: {
      items: [],
      lastUpdated: new Date()
    }
  });

  React.useEffect(() => {
    reloadData(setSensorData);
  }, []);

  return (
    <Container maxWidth="sm">
    {state.sensorData &&
          <SensorList reloadData={reloadData} sensorData={state.sensorData}/>
        }
    </Container>
  );
}
