import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { Collapse } from '@material-ui/core';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import { Map } from 'react-lodash';
import SnoozeIcon from '@material-ui/icons/Snooze';

export default function NotificationEventsGroup({ eventsList, handleClickEvents, snoozeFunction }) {

  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

  const epochToTime = (epoch) => {
    const d = new Date(0);
    d.setUTCSeconds(epoch / 1000);
    return d.toLocaleTimeString();
  }
  
    return (
      <div>
        <ListItem>
          <ListItemText primary={eventsList[0].name} secondary={`${eventsList.length} entries`} />
          <ListItemText primary={epochToTime([...eventsList].reverse()[0].timestamp)} />
          <SnoozeIcon onClick={() => snoozeFunction(eventsList[0].sensorId)} />
          { open ? <ExpandLess onClick={handleClick} /> : <ExpandMore onClick={handleClick} /> }
        </ListItem>
        <Collapse in={open} timeout="auto" unmountOnExit>
          <Map collection={eventsList} iteratee={entry =>
            <ListItem button onClick={handleClickEvents} key={`${entry.sensorId}-${entry.timestamp}`}>
              <ListItemText inset secondary={epochToTime(entry.timestamp)} primary={entry.message} />
            </ListItem>
          } />
        </Collapse>
      </div>
    )
}